import React from "react";
import { Link } from "react-router-dom";
import {
  Navbar,
  Collapse,
  Nav,
  NavItem,
  Button,
} from "reactstrap";

const Header = () => {
  const [isOpen, setIsOpen] = React.useState(false);
  const Handletoggle = () => {
    setIsOpen(!isOpen);
  };
  const showMobilemenu = () => {
    document.getElementById("sidebarArea").classList.toggle("showSidebar");
  };
  return (
    <Navbar color="primary" dark expand="md" className="fix-header">
      <div className="d-flex align-items-center">
       <font size="5" color="white">전국철도노동조합 부곡차량지부</font>
        <Button
          color="primary"
          className=" d-lg-none"
          onClick={() => showMobilemenu()}
        >
          <i className="bi bi-list"></i>
        </Button>
      </div>
      <div className="hstack gap-2">
        <Button
          color="primary"
          size="sm"
          className="d-sm-block d-md-none"
          onClick={Handletoggle}
        >
          {isOpen ? (
            <i className="bi bi-x"></i>
          ) : (
            <i className="bi bi-three-dots-vertical"></i>
          )}
        </Button>
      </div>

      <Collapse navbar isOpen={isOpen}>
        <Nav className="me-auto" navbar>
          <NavItem>
            <Link to="/starter" className="nav-link">
              Dashboard
            </Link>
          </NavItem>
          <NavItem>
            <Link to="/P1table_2023" className="nav-link">
              노사협의
            </Link>
          </NavItem>
          <NavItem>
            <Link to="/P2table_2023" className="nav-link">
              산보위
            </Link>
          </NavItem>
          <NavItem>
            <Link to="/Wtable" className="nav-link">
              분할연차
            </Link>
          </NavItem>
          <NavItem>
            <Link to="/Ctable" className="nav-link">
              청소시간
            </Link>
          </NavItem>
          <NavItem>
            <Link to="/Otable" className="nav-link">
              임원소개
            </Link>
          </NavItem>
        </Nav>
      </Collapse>
    </Navbar>
  );
};

export default Header;
